@font-face {
  font-family: "Bebas Neue";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/BebasNeue-Regular.woff2 ") format("woff2"),
    url("../fonts/BebasNeue-Regular.woff") format("woff");
}

@font-face {
  font-family: "Bebas Neue";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/BebasNeue-Bold.woff2 ") format("woff2"),
    url("../fonts/BebasNeue-Bold.woff") format("woff");
}

